import React, { useEffect, useState } from "react";
import dimention from "@styles/assets/images/hirescreens/media-icons/dimention.svg";
import brandfolder from "@styles/assets/images/hirescreens/media-icons/brandfolder.svg";
import affinityphoto from "@styles/assets/images/hirescreens/media-icons/affinityphoto.svg";
import gravity_designer from "@styles/assets/images/hirescreens/media-icons/gravity_designer.svg";
import milanote from "@styles/assets/images/hirescreens/media-icons/milanote.svg";

import corelDraw from "@styles/assets/images/hirescreens/hire-graphic-designers/simple-icons_coreldraw.svg";
import figma from "@styles/assets/images/hirescreens/hire-product-designers/figma.svg";
import Photoshop from "@styles/assets/images/hirescreens/hire-graphic-designers/adobe_photoshop.svg";
import affinity from "@styles/assets/images/hirescreens/hire-graphic-designers/vscode-icons_file-type-affinitydesigner.svg";
import notion from "@styles/assets/images/hirescreens/hire-product-designers/notion.svg";

import Ticker from "@components/common/Ticker";

const datas = [
  { id: 0, title: "Adobe Dimension", icon: dimention },
  { id: 1, title: "Photoshop", icon: Photoshop },
  { id: 2, title: "Brandfolder", icon: brandfolder },
  { id: 3, title: "Affinity Photo", icon: affinityphoto },
  { id: 4, title: "Gravit Designer", icon: gravity_designer },
  { id: 5, title: "Figma", icon: figma },
  { id: 6, title: "Milanote", icon: milanote },
  { id: 7, title: "Affinity Designer", icon: affinity },
  { id: 8, title: "Notion", icon: notion },
  { id: 9, title: "CorelDRAW", icon: corelDraw },
];
const TechStackMarquee = () => {
  const [scrollDir, setScrollDir] = useState(null);

  useEffect(() => {
    let scrollSide = window.scrollY;

    const handleScroll = () => {
      let currentScrollSide = window.scrollY;

      if (currentScrollSide > scrollSide) {
        setScrollDir("down");
      } else if (currentScrollSide < scrollSide) {
        setScrollDir("up");
      }
      scrollSide = currentScrollSide;
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <section className="hire-marquee-wrapper">
      <div className="hire-developers-hero-wrapper item-center gap-lg">
        <div className="item-center gap-lg">
          <h2 className="about-h2">Our Technology Stack</h2>
          <p className="about-p c-text-center">
            Our Brand Identity Designers use a strategic design stack to build
            cohesive brand systems—ensuring every visual element reflects your
            brand’s story, values, and visual identity.
          </p>
        </div>
        <section className="token-dev-technology-marquee-section">
          <Ticker
            duration={30}
            direction={`${scrollDir === "up" ? 100 : -100}`}
          >
            {datas.map((item) => (
              <div
                className="token-dev-technology-card item-center"
                key={item.id}
              >
                <div className="flex-center">
                  <img src={item.icon} alt={item.title} />
                  <p className="about-p">{item.title}</p>
                </div>
              </div>
            ))}
          </Ticker>
          <Ticker
            duration={30}
            direction={`${scrollDir === "up" ? -100 : 100}`}
          >
            {datas.map((item) => (
              <div
                className="token-dev-technology-card item-center"
                key={item.id}
              >
                <div className="flex-center">
                  <img src={item.icon} alt={item.title} />
                  <p className="about-p">{item.title}</p>
                </div>
              </div>
            ))}
          </Ticker>
        </section>
      </div>
    </section>
  );
};

export default TechStackMarquee;
